html {
    scroll-behavior: smooth;
  }

body::-webkit-scrollbar {
    width: 10px;
  }
   
  body::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(233, 76, 76, 0.3);
  }
   
  body::-webkit-scrollbar-thumb {
    background-color: darkgrey;
    background-color: rgb(32, 182, 154);
    /* outline: 1px solid slategrey; */
    /* border-top-left-radius: 50px;
    border-top-right-radius: 50px;
    border-bottom-right-radius: 50px;
    border-bottom-left-radius: 50px; */
  }
.Mabout {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 400vh;
    height: 398vh;
    align-items: center;
    overflow: none;
}

@media only screen and (max-width: 750px) {
    .Mabout {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 400vh;
        height: 398vh;
        height: auto;
        align-items: center;
        overflow: none;
    }
}

.heading {
    /* height: auto; */
    height: 50vh;
    width: 100%;
    display: flex;
    align-items: center;
}

.heading .sub1 {
    margin: 20px;
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    /* background-color: red; */
}

.heading .sub1>h1 {
    font-size: 80px;
}

.heading .sub1>h1>span {
    color: white;
    font-size: 80px;
    text-shadow: -2px 2px 0 rgb(0, 0, 0), 2px 2px 0 rgb(0, 0, 0), 2px -2px 0 rgb(0, 0, 0), -2px -2px 0 rgb(0, 0, 0);
}

.heading .sub1 .Pixels {
    color: white;
    /* color: rgb(0, 0, 0); */
    font-size: 80px;
    text-shadow: -2px 2px 0 rgb(32, 182, 154), 2px 2px 0 rgb(32, 182, 154), 2px -2px 0 rgb(32, 182, 154), -2px -2px 0 rgb(32, 182, 154);
}

.heading .sub2 {
    width: 60%;
    margin: 20px;
}

@media only screen and (max-width: 750px) {
    .heading .sub1>h1 {
        font-size: 50px;
        margin: 30px;
    }
    .heading .sub1>h1 {
        font-size: 60px;
    }
    .heading .sub1>h1>span {
        color: white;
        font-size: 60px;
        text-shadow: -2px 2px 0 rgb(0, 0, 0), 2px 2px 0 rgb(0, 0, 0), 2px -2px 0 rgb(0, 0, 0), -2px -2px 0 rgb(0, 0, 0);
    }
    .heading .sub1 .Pixels {
        color: white;
        /* color: rgb(0, 0, 0); */
        font-size: 60px;
        text-shadow: -2px 2px 0 rgb(32, 182, 154), 2px 2px 0 rgb(32, 182, 154), 2px -2px 0 rgb(32, 182, 154), -2px -2px 0 rgb(32, 182, 154);
    }
    .heading {
        /* height: auto; */
        height: 30vh;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .heading .sub1 {
        /* margin: 20px; */
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        /* flex-direction: column; */
        /* background-color: red; */
    }
}

/* ------------- section 2 ------------------------- */

.Card {
    height: 100vh;
    width: 100%;
    font-family: 'Open Sans Condensed', sans-serif;
}

.Card_sub1 {
    height: 50vh;
    width: 100%;
    display: flex;
}

.CardBox {
    height: 50vh;
    width: 100%;
    padding: 20px;
}

.CardBox>h1, p {
    font-family: 'Open Sans Condensed', sans-serif;
}

.CardBox>h1 {
    font-size: 45px;
    line-height: 40.5px;
    word-spacing: 4px;
    padding: 10px 0px;
    font-weight: 300;
}

.CardBox:hover {
    color: rgb(32, 182, 154);
    color: white;
    background-color: #c4c4c4;
    background-color: black;
    background-color: rgb(51, 51, 51);
}

.CardBox:hover>h1 {
    color: rgb(32, 182, 154);
    /* background-color: black; */
}

.CardBox:hover>h1 {
    color: rgb(255, 255, 255);
    /* background-color: black; */
}

.CardBox>p {
    font-size: 20px;
    word-spacing: 2px;
    padding: 10px 0px;
    /* background-color: white; */
}

.Card_sub2 {
    height: 50vh;
    width: 100%;
    display: flex;
}

/* --------------------resopnsive of section 2 -------------*/

@media only screen and (max-width: 750px) {
    .Card {
        height: auto;
        width: 100%;
        font-family: 'Open Sans Condensed', sans-serif;
    }
    .Card_sub1 {
        height: auto;
        width: 100%;
        display: flex;
        flex-direction: column;
        margin: 20px 0;
    }
    .CardBox {
        height: auto;
        width: 100%;
        padding: 20px;
    }
    .CardBox>h1, p {
        font-family: 'Open Sans Condensed', sans-serif;
    }
    .CardBox>h1 {
        font-size: 45px;
        line-height: 40.5px;
        word-spacing: 4px;
        padding: 10px 0px;
        font-weight: 300;
    }
    .CardBox:hover {
        color: white;
        color: rgb(32, 182, 154);
        background-color: black;
        /* background-color: #c4c4c4; */
    }
    .CardBox:hover>h1 {
        color: rgb(32, 182, 154);
        /* background-color: black; */
    }
    .CardBox:hover>h1 {
        color: rgb(255, 255, 255);
        /* background-color: black; */
    }
    .CardBox>p {
        font-size: 20px;
        word-spacing: 2px;
        padding: 10px 0px;
        /* background-color: white; */
    }
    .Card_sub2 {
        height: auto;
        width: 100%;
        display: flex;
        flex-direction: column;
    }
}

/* -----------------------  Section 3333  ---------------------- */

.section3 {
    /* height: auto; */
    height: 70vh;
    width: 100%;
    display: flex;
    align-items: center;
}

.section3 .sub1 {
    /* height: auto; */
    width: 50%;
    height: 70vh;
    display: flex;
    /* background-color: seagreen; */
    /* background-color: rgb(32, 182, 154); */
    align-items: center;
    justify-content: center;
}

.section3 .sub1>h1 {
    font-size: 60px;
    color: white;
    font-weight: 600;
}

.section3 .sub1>img {
    height: 500px;
    /* width: 500px; */
    width: 100%;
}

.section3 .sub2 {
    /* height: auto; */
    width: 50%;
    height: 70vh;
    /* background-color: #c4c4c4; */
