/* ____________sub 2_____________________ */

.OurWork .section1 .sub2 {
    height: 90vh;
    width: 100%;
    display: grid;
    grid-template-columns: auto auto auto;
    padding: 10px;
    align-items: center;
    justify-content: center;
    padding: 20px;
}

.sub2 .card {
    width: 360px;
    height: 360px;
    padding: 1.5rem;
    position: relative;
    display: flex;
    align-items: flex-end;
    transition: 0.4s ease-out;
    box-shadow: 0px 7px 10px rgba(black, 0.5);
    overflow: hidden;
    margin: 15px;
}

.sub2 .card:hover>.card__img {
    -webkit-transform: scale(1.13);
            transform: scale(1.13);
    -webkit-filter: brightness(60%);
}

.OurWork .section1 .sub2 .card:hover::before {
    opacity: 1;
}

.sub2 .card:hover>.info {
    opacity: 1;
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    -webkit-filter: brightness(100%);
}

.sub2 .card>img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0;
    transition: 1s;
}

.sub2 .card .info {
    position: relative;
    z-index: 3;
    color: white;
    opacity: 0;
    -webkit-transform: translateY(30px);
            transform: translateY(30px);
    transition: 0.5s;
    z-index: 1000;
}

.sub2 .card .info>h1 {
    margin: 0px;
    color: white;
}

.sub2 .card .info>p {
    letter-spacing: 1px;
    font-size: 15px;
    margin-top: 8px;
}

