    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 75px;
    transition-timing-function: ease-in;
    transition: all 0.5s;
    z-index: 1;
    box-shadow: 0px 4px 10px rgba(92, 92, 92, 0.349);
    position: fixed;
}

